<template>
  <div class="cascadeTerm">
    <strong style="width: 100px;display: inline-block;text-align: right;padding-right: 10px;">行政区划：</strong>
    <div class="cascadeRec_box" style="padding-left:100px;margin-top: -22px;">
        <p v-for="(item,index) in listData" :key = "index" >
          <span :class="{active : activeId === -1}" @click="clickProvince(item)">全省</span>
          <span v-for="(item2,index) in item.children" :key = "index"  :class="{active : activeId === index}" @click="clickCity(item2,index)">
            {{item2.label}}
          </span>
        </p>
      <p v-show="show" style="margin-top: 10px;padding-left: 10px">
          <span v-for="(item,index) in showData" :key = "index"  :class="{active :activeId2 === index}" @click="clickCounty(item,index)">
          {{item.label}}
          </span>
      </p>
    </div>
  </div>
</template>

<script>
import { getMenu } from '@/api/acquisitionTable'
import { getToken } from '@/utils/auth'
export default {
  name: 'CascadeTerm',
  data () {
    return {
      listData: [],
      show: false,
      activeId: -2,
      activeId2: -2,
      showData: []
    }
  },
  mounted () {
    this.getMenu()
  },
  methods: {
    /* 获取省市县级联菜单 */
    getMenu () {
      getMenu({
        accessToken: getToken(),
        validate: true,
        data: {
        }
      }).then((res) => {
        this.listData = res.data.data
      }).catch((error) => {
        console.log(error)
      })
    },
    clickProvince (item) {
      /* 如果已选中，关闭高亮和下级菜单 */
      if (this.activeId === -1) {
        /* 删除对应字段的查询数据 */
        this.$emit('delQueryTrem', 'code')
        this.show = false
        this.activeId = -2
        this.activeId2 = -2
      } else {
        /* 打开下级菜单 */
        this.show = false
        /* 赋值下级菜单数据 */
        this.showData = []
        for (let i in item.children) {
          this.showData = this.showData.concat(item.children[i].children)
        }
        /* 高亮index */
        this.activeId = -1
        this.activeId2 = -2
        /* 将显示的数据传回查询显示中的queryData  */
        this.labelValue = item.label
        let obj = {
          name: '行政区划',
          text: '全省',
          key: 'code',
          value: item.comCode
        }
        this.updataQueryData(obj)
      }
    },
    clickCity (item, index) {
      if (this.activeId === index) {
        /* 删除对应字段的查询数据 */
        this.$emit('delQueryTrem', 'code')
        this.show = false
        this.activeId = -2
        this.activeId2 = -2
      } else {
        this.show = true
        /* 生成下级菜单数据 */
        this.showData = item.children
        /* 高亮index */
        this.activeId = index
        this.activeId2 = -2
        /* 将显示的数据传回查询显示中的queryData  */
        this.labelValue = item.label
        let obj = {
          name: '行政区划',
          text: this.labelValue,
          key: 'code',
          value: item.comCode
        }
        this.updataQueryData(obj)
      }
    },
    clickCounty (item, index) {
      /* 高亮index */
      this.activeId2 = index
      /* 将显示的数据传回查询显示中的queryData  */
      this.labelValue = item.label
      let obj = {
        name: '行政区划',
        text: this.labelValue,
        key: 'code',
        value: item.comCode
      }
      this.updataQueryData(obj)
    },
    /* updataQueryData */
    updataQueryData (obj) {
      this.$emit('updataQueryData', obj)
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .cascadeTerm{
    strong{
      font-weight: normal;
    }
    .cascadeRec_box{
      span{
        display: inline-block;margin-right: 10px;margin-left:0;cursor: pointer;padding:2px 10px;
      }
    }
    .active{
      background: #61A7F3;border-radius: 4px;color:#fff;margin-left: 5px
    }
  }
</style>
